<template>
  <div class="videoDetail">
    <div class="mescroll" id="list">
      <div id="listData">
        <div class="top">
          <!-- <video class="banner" autoplay controls>
        <source :src="detail.dvideo" type="video/m8u3"> 您的浏览器不支持 video 标签。
      </video> -->
          <div class="bvideo">
            <video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions" @play="onPlayerPlay($event)" @pause="onPlayerPause($event)"></video-player>
          </div>
          <div class="content">
            <p class="name">{{detail.viewName}}</p>
            <div class="flex">
              <div class="text1">{{`${detail.dvisitNum}人浏览`}}</div>
              <div class="text2">{{detail.aoutPrice?'￥'+detail.aoutPrice.toFixed(2):'免费'}}</div>
            </div>
          </div>
        </div>
        <div class="mid">
          <van-tabs v-model="active" :line-width="20" :line-height="3" :sticky="true" :swipe-threshold="6" :border="false" :title-active-color="'#000'" @click="onClick">
            <van-tab title="详情">
              <div class="detail">
                <p class="title">包含该课程的还有</p>
                <div class="list">
                  <div class="item" v-for="(item,index) in sectionList" :key="index" @click="goVideoDetail(item)">
                    <div class="left">
                      <img :src="item.dimg?$utils.dealImg(item.dimg):require('@/assets/img/default/defaultH.png')" alt />
                    </div>
                    <div class="right">
                      <p class="name">{{item.viewName}}</p>
                      <div class="item-bottom">
                        <div class="text1">{{`${item.dvisitNum}人浏览`}}</div>
                        <div class="text2">{{item.aoutPrice?'￥'+item.aoutPrice.toFixed(2):'免费'}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </van-tab>
            <van-tab title="评论">
              <div class="comment">
                <div class="comment-top">
                  <img :src="detail.shopHeader?$utils.dealImg(detail.shopHeader):require('../../assets/img/default/userDefault.png')" alt />
                  <div class="right">
                    <p class="name">{{detail.shopName}}</p>
                    <div class="content">
                      <p>欢迎进入直播间：</p>
                      <p>1、请自行调节手机音量至合适的状态。</p>
                    </div>
                  </div>
                </div>
                <div class="list">
                  <div class="item" v-for="(item,index) in evaluateList" :key="index">
                    <div class="left">
                      <img :src="item.userHeader?$utils.dealImg(item.userHeader):require('../../assets/img/default/userDefault.png')" alt />
                    </div>
                    <div class="right">
                      <div class="name">{{item.userName}}</div>
                      <div class="info">
                        <span class="time">{{getTimeText(item.createDate)}}</span>
                        <span class="del" v-if="item.userCode == userCode" @click="del(item.dcode)">删除</span>
                      </div>
                      <div class="message">{{item.dmsg}}</div>
                    </div>
                  </div>
                </div>
              </div>
            </van-tab>
            <van-tab title="相关推荐">
              <div class="recommend">
                <p class="title">精品课程</p>
                <div class="list">
                  <div class="item" v-for="(item,index) in recommendList" :key="index" @click="goDetail(item)">
                    <div class="left">
                      <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
                    </div>
                    <div class="right">
                      <p class="name">{{item.viewName}}</p>
                      <div class="item-bottom">
                        <div class="text1">{{item.aoutPrice?`${item.asaleNum}人开通`:`${item.avisitNum}人浏览`}}</div>
                        <div class="text2">{{item.aoutPrice?'￥'+item.aoutPrice.toFixed(2):'免费'}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </van-tab>
          </van-tabs>
        </div>
      </div>
    </div>
    <div class="bottom">
      <van-tabbar v-model="active1" active-color="#000" inactive-color="#000">
        <van-tabbar-item @click="tap('1')">
          <span>课程目录</span>
          <img slot="icon" src="../../assets/img/college/icon5.png" />
        </van-tabbar-item>
        <van-tabbar-item @click="show = true">
          <span>写评论</span>
          <img slot="icon" src="../../assets/img/college/icon6.png" />
        </van-tabbar-item>
        <van-tabbar-item @click="moreShow = true">
          <span>更多</span>
          <img slot="icon" src="../../assets/img/college/icon7.png" />
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <div class="borderRad">
      <van-popup v-model="moreShow" position="bottom">
        <div class="popup" @click="moreShow = false">
          <div class="popup-top">
            <p class="text">更多</p>
            <p class="icon">×</p>
          </div>
          <div class="popup-mid">
            <div class="list">
              <div class="item" @click="toIndex">
                <img src="../../assets/img/college/icon8.png" alt />
                <p>首页</p>
              </div>
              <div class="item" @click="shareShow = true">
                <img src="../../assets/img/college/icon9.png" alt />
                <p>分享好友</p>
              </div>
              <div class="item" @click="collect">
                <img src="../../assets/img/college/icon10.png" alt />
                <p>收藏</p>
              </div>
              <div class="item">
                <img src="../../assets/img/college/icon11.png" alt />
                <p>投诉</p>
              </div>
            </div>
          </div>
        </div>
      </van-popup>
    </div>
    <van-popup v-model="show" position="bottom">
      <div class="popup">
        <div class="flex">
          <div class="left">
            <van-field v-model="message" placeholder="发评论" />
          </div>
          <div class="btn" @click="send">发送</div>
        </div>
      </div>
    </van-popup>
    <div class="mask" v-if="shareShow" @click="shareShow=false">
      <img src="../../assets/img/activity/shang.png" alt="">
    </div>
  </div>
</template>
<script>
import { Tab, Tabs, Tabbar, TabbarItem, Popup, Field, Dialog } from "vant";
import "videojs-flash"; // 引入才能播放rtmp视频
import "videojs-contrib-hls"; // 引入才能播放m3u8文件
import { videoPlayer } from "vue-video-player";
import {
  getDirectoryDetail,
  collectSave,
  getSectionList,
  getSectionRecommendList,
  addTime,
  getEvaluateList,
  wxApi,
  saveEvaluate,
  delEvaluate,
} from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
    [Popup.name]: Popup,
    [Field.name]: Field,
    videoPlayer,
  },
  data() {
    return {
      active: 0,
      active1: 0,
      id: "",
      moreShow: false,
      show: false,
      message: "",
      detail: {},
      playerOptions: {
        //       playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: false, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        techOrder: ["flash", "html5"], // 兼容顺序
        lash: { hls: { withCredentials: false } }, //可以使用rtmp视频
        html5: { hls: { withCredentials: false } }, //可以使用m3u8视频
        isFullscreen: true,
        sources: [],
        poster: "", //你的封面地址
        width: document.documentElement.clientWidth,
        notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true, //全屏按钮
        },
      },
      mescroll: null,
      sectionList: [],
      evaluateList: [],
      recommendList: [],
      Interval: null,
      time: 0,
      shareShow: false,
      userCode: "",
    };
  },
  methods: {
    onClick() {
      if (this.mescroll) {
        this.mescroll.scrollTo(0, 0);
        this.mescroll.resetUpScroll();
      } else {
        this.$nextTick(() => {
          this.startMescroll();
        });
      }
    },
    goVideoDetail(item) {
      clearInterval(this.Interval);
      this.saveTime();
      sessionStorage.setItem("directoryId", item.id);
      this.id = sessionStorage.getItem("directoryId")
        ? sessionStorage.getItem("directoryId")
        : this.$route.query.acode;
      this.active = 0;
      this.mescroll.destroy();
      this.mescroll = null;
      this.Interval = null;
      this.time = 0;
      this.loadData();
    },
    goDetail(item) {
      sessionStorage.setItem("acode", item.acode);
      this.$router.back();
    },
    tap(type) {
      switch (type) {
        case "1":
          sessionStorage.setItem("active", 1);
          this.$router.back();
          break;
        default:
          console.log(1);
          break;
      }
    },
    collect() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      collectSave({ learnCode: this.detail.acode, favType: "1" }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("收藏成功");
        } else {
          this.$toast.fail(res.errorMsg);
        }
      });
    },
    toIndex() {
      this.$router.push({
        name: "collegeIndex",
      });
    },
    send() {
      if (!this.message) {
        this.$toast("请输入评价");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      saveEvaluate({
        acode: this.detail.acode,
        dmsg: this.message,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          if (this.active == 1) {
            this.mescroll.resetUpScroll();
          }
          this.$toast("评价成功");
          this.show = false;
        } else {
          this.$toast(res.msg);
        }
      });
    },
    del(dcode) {
      Dialog.confirm({
        title: "提示",
        message: "是否删除此条评价？",
      })
        .then(() => {
          // on confirm
          const loading = this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true, // 禁用背景点击
          });
          delEvaluate({
            dcode: dcode,
          }).then(res => {
            loading.clear();
            if (res.code == 0) {
              this.$toast("删除成功");
              this.mescroll.resetUpScroll();
            } else {
              this.$toast(res.msg);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    onPlayerPlay(player) {
      console.log("player");
      this.Interval = setInterval(() => {
        this.time++;
      }, 1000);
    },
    onPlayerPause(player) {
      clearInterval(this.Interval);
    },
    saveTime() {
      if (this.time > 0) {
        addTime({
          learnTime: this.time,
          dcode: this.detail.dcode,
        }).then(res => {
          if (res.result == "false") {
            this.$toast.fail(res.errorMsg);
          }
        });
      }
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getDirectoryDetail({ dcode: this.id }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
          setTimeout(() => {
            this.startMescroll();
          }, 500);
          this.playerOptions.poster = this.$utils.dealImg(res.data.dimg);
          this.playerOptions.sources = [
            {
              type: "application/x-mpegURL",
              // src:
              //   "http://vedio.jungang.china-mail.com.cn/781c176b8bbb469e932926e7d2eaf61c/722ce54ff7384283863b320b39f4866e-d8c14e58bf907d30370fc69ed4d977b2-ld.m3u8"
              src: res.data.dvideo,
            },
          ];
        } else {
          this.$toast.fail(res.errorMsg);
        }
      });
    },
    getList(pageNum, pageSize, successCallback, errorCallback) {
      let dataObj = {};
      if (this.active == 0) {
        dataObj = {
          acode: this.detail.acode,
          pageNum: pageNum,
          pageSize: pageSize,
        };
        getSectionList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data, "sectionList");
            } else {
              $this.$toast.fail(res.errorMsg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      } else if (this.active == 1) {
        dataObj = {
          acode: this.detail.acode,
          pageNum: pageNum,
          pageSize: pageSize,
        };
        getEvaluateList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data, "evaluateList");
            } else {
              $this.$toast.fail(res.errorMsg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      } else if (this.active == 2) {
        dataObj = {
          cateCode: this.detail.cateCode,
          pageNum: pageNum,
          pageSize: pageSize,
        };
        getSectionRecommendList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data, "recommendList");
            } else {
              $this.$toast.fail(res.errorMsg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      } else {
        console.log(111);
      }
    },
    startMescroll() {
      this.mescroll = new MeScroll(`list`, {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: `listData`,
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据",
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true, // 是否开启懒加载,默认false
          },
        },
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.getList(
        page.num,
        page.size,
        function(curPageData, name) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData, name);
          if (page.num == 1) $this[name] = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this[name].push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    },
    share() {
      let that = this;
      wxApi()
        .then(res => {
          console.log(this.$wx, res.data);
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
          });
          this.$wx.ready(function() {
            that.$wx.onMenuShareAppMessage({
              title: that.detail.dname, // 分享标题
              desc: that.detail.dname2, // 分享描述
              link:
                that.$config.http +
                "/wx/#/videoDetail?acode=" +
                that.detail.dcode, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.dimg), // 分享图标
              type: "link", // 分享类型,music、video或link，不填默认为link
              dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
              success: function() {
                // 用户确认分享后执行的回调函数
                that.shareShow = false;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
            that.$wx.onMenuShareTimeline({
              title: that.detail.dname, // 分享标题
              link:
                that.$config.http +
                "/wx/#/videoDetail?acode=" +
                that.detail.dcode, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.dimg), // 分享图标
              success: function() {
                that.shareShow = false;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    getTimeText(date) {
      var today = new Date();
      today.setHours(0);
      today.setMinutes(0);
      today.setSeconds(0);
      today.setMilliseconds(0);
      //给出时间 - 今天0点
      var offset = new Date(date).getTime() - today.getTime(),
        offset1 = new Date().getTime() - new Date(date).getTime();
      var isToday = offset / 1000 / 60 / 60,
        isToday1 = offset1 / 1000 / 60 / 60;
      if (isToday1 > 0 && isToday1 <= 1) {
        return "刚刚";
      } else if (isToday > 0 && isToday <= 24) {
        return "今天";
      } else if (isToday < 0 && isToday <= -24) {
        return "昨天";
      } else {
        return date;
      }
    },
  },
  mounted() {},
  activated() {
    this.userCode = sessionStorage.getItem("userId");
    this.id = sessionStorage.getItem("directoryId")
      ? sessionStorage.getItem("directoryId")
      : this.$route.query.acode;
    this.loadData();
    this.share();
  },
  deactivated() {
    clearInterval(this.Interval);
    this.saveTime();
  },
};
</script>
<style lang="scss" scoped>
.videoDetail {
  height: calc(100vh - 50px);
  .top {
    .banner {
      width: 100vw;
      display: block;
    }
    .content {
      background: #fff;
      padding: 10px;
      .name {
        font-size: 15px;
        font-weight: bold;
        line-height: 25px;
      }
      .flex {
        margin: 5px 0 0;
        display: flex;
        align-items: center;
        .text1 {
          color: #999;
          font-size: 13px;
          flex: 2;
        }
        .text2 {
          flex: 1;
          text-align: right;
          color: #9e1435;
          font-size: 13px;
        }
      }
    }
  }
  .mid {
    background: #fff;
    .detail {
      padding: 10px;
      .title {
        font-weight: bold;
        line-height: 20px;
        margin: 5px 0;
      }
      .list {
        .item {
          border-bottom: 1pt solid #efefef;
          margin-bottom: 10px;
          padding: 5px 0;
          display: flex;
          .left {
            img {
              width: 120px;
              margin-right: 5px;
            }
          }
          .right {
            position: relative;
            flex: 2;
            .name {
              font-size: 13px;
              line-height: 20px;
              max-height: 50px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .item-bottom {
              position: absolute;
              bottom: 0;
              left: 0;
              display: flex;
              width: 100%;
              line-height: 30px;
              .text1 {
                color: #999;
                font-size: 13px;
                flex: 2;
              }
              .text2 {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
    }
    .comment {
      padding: 10px;
      .comment-top {
        display: flex;
        padding: 10px 0;
        background: #fff;
        margin: 0 0 10px;
        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          margin-right: 5px;
        }
        .right {
          .name {
            font-size: 13px;
            line-height: 30px;
            color: #666;
          }
          .content {
            font-size: 15px;
